.blog {
    padding: 8rem 0;
    display: grid;
    grid-template-columns: 1fr;
    gap: 2.4rem;
    justify-content: space-between;

    .articulo {
        display: grid;
        grid-template-columns: 1fr;
        box-shadow: 0px 4px 10px 0px rgba($color: #000000, $alpha: .2);

        
        .imagen {
            width: 100%;
            height: auto;

            picture,
            img {
                height: auto;
                width: 100%;
            }
        }

        .articulo-text {
            padding: 2.4rem;

            h4 {
                text-align: left;
            }

            p {
                margin-top: 2.4rem;
            }
        }
    }
}