.blog {
  padding: 8rem 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2.4rem;
  justify-content: space-between;
}
.blog .articulo {
  display: grid;
  grid-template-columns: 1fr;
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.2);
}
.blog .articulo .imagen {
  width: 100%;
  height: auto;
}
.blog .articulo .imagen picture,
.blog .articulo .imagen img {
  height: auto;
  width: 100%;
}
.blog .articulo .articulo-text {
  padding: 2.4rem;
}
.blog .articulo .articulo-text h4 {
  text-align: left;
}
.blog .articulo .articulo-text p {
  margin-top: 2.4rem;
}